import { Component, OnInit } from '@angular/core';
import * as $   from 'jquery' ;
import { TextAnimation } from 'ngx-teximate';
import { fadeInLeft , fadeInRight , fadeInDown , fadeInUp  } from 'ng-animate';
import {FormBuilder, FormGroup, FormControl ,Validators, NgForm} from '@angular/forms';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import {DialogLoadingComponent} from './dialog-loading/dialog-loading.component'


@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {

  public email ;
  public form ;


  enterAnimation: TextAnimation = {
    animation: fadeInDown,
    delay: 50  ,
    type: 'word'
  };

  whiteAnimation: TextAnimation = {
    animation: fadeInDown,
    delay: 50  ,
    type: 'word'
  };

  yellowAnimation: TextAnimation = {
    animation: fadeInRight,
    delay: 50  ,
    type: 'letter'
  };

  darkAnimation: TextAnimation = {
    animation: fadeInUp,
    delay: 50  ,
    type: 'word'
  };

  constructor( public dialog: MatDialog , private http : HttpClient , private router : Router) {
    this.email = {
      name : '' ,
      lastname : '' ,
      mail : '' ,
      subject : '' ,
      content : '' ,
      pattern : "[A-Za-z]+"
    } ;
  }


  ngOnInit() {
    $(".activeItem").removeClass('activeItem') ;
    $(".contactSidenav").addClass('activeItem') ;
    $(document).ready(()=>{
      $(".form_mailer").slideUp(0)
      setTimeout(()=>{
        setTimeout(function(){
          $(".text_div").fadeIn(0) ;
          $(".animate_all").click() ;
        } , 2000) ;
      } , 600) ;
      setTimeout(()=>{
        $(".one_contact").fadeIn(500) ;
        setTimeout(()=> {
          $(".form_mailer").slideDown(1000) ;
        } , 500) ;

      } , 3500 ) ;
    })
  }

  AnimationsText(table) {
    table.forEach(element => {
      element.enterPlayer.play() ;
    });

  }

  onSubmit(form : NgForm){
    if( form.valid){
      this.form = form ;
      this.openDialog() ;
    }
  }

  openDialog(): void {
    const dialogRef = this.dialog.open(DialogLoadingComponent, {
      width: '250px',
      panelClass: 'dialog_window' ,
      data: {email: this.email}
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result == 'yep') {
        this.form.reset() ;
      }
    });
  }

}
