import { ContactComponent } from './../contact.component';
import { HttpClient, HttpErrorResponse, HttpResponse } from '@angular/common/http';
import { Router } from '@angular/router';
import { Component, OnInit , Inject } from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';


@Component({
  selector: 'app-dialog-loading',
  templateUrl: './dialog-loading.component.html',
  styleUrls: ['./dialog-loading.component.scss']
})
export class DialogLoadingComponent implements OnInit {
  public hidden_form = true ;
  public succee_msg = false ;
  public error_msg = false ;
  public email ;
  constructor(
    public dialogRef: MatDialogRef<ContactComponent>,
    @Inject(MAT_DIALOG_DATA) public data : any , private router : Router  , private http : HttpClient ) {
      this.hidden_form = true ;
      this.succee_msg = false ;
      this.error_msg = false ;
      dialogRef.disableClose = true;
      this.email = this.data.email ;
      console.log(this.email) ;

      var fd = new FormData() ;

      fd.append("name" , this.email.name) ;
      fd.append("lastname" , this.email.lastname) ;
      fd.append("email" , this.email.mail) ;

      fd.append("subject" , this.email.subject) ;
      fd.append("message" , this.email.content) ;


      this.http.post('https://abdennourbadeche.com/ws/public/api/contact-us' , fd)
      .subscribe(
        (response : any) => {
          console.log(response);
          this.hidden_form = false ;
          this.succee_msg = true ;
        } ,
        (err : HttpErrorResponse) => {
          console.log(err);
          this.hidden_form = false ;
          this.succee_msg = false ;
          this.error_msg = true ;

        }
      ) ;


    }

  onNoClick(): void {
    this.dialogRef.close('nop') ;
  }
  onNoClick2(): void {
    this.dialogRef.close('yep') ;
  }

  ngOnInit() {
  }

}
